
.preview-shop {
  display: none;

  @media screen and (min-width: 768px) {
    height: 100%;
    display: block;
    margin-left: 475px;
  }

  @media screen and (min-width: 1200px) {
    margin-left: 700px;
  }

  .preview-shop-item {
    width: 60px;
    background-image: url(../images/preview/shop-item-bg.png);
    background-repeat: no-repeat;
    background-position: right 0 top 0;
    margin-bottom: 13px;

    &:last-child {
      margin-bottom: 0;
    }

    @media screen and (min-width: 1200px) {
      background-image: url(../images/preview/shop-item-bg-desc.png);
      width: 90px;
    }
  }

  .preview-shop-item p:first-child {
    padding-left: 15px;
    font-family: Titan One;
    font-size: 28px;
    line-height: 32px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #d41443;

    @media screen and (min-width: 1200px) {
      font-size: 46px;
      line-height: 53px;
    }
  }

  .preview-shop-item p:nth-child(2) {
    padding-right: 12px;
    text-align: right;
    font-family: DM Sans;
    font-size: 8px;
    line-height: 10px;
    color: #ffffff;

    @media screen and (min-width: 1200px) {
      font-size: 14px;
      line-height: 18px;
    }
  }
}

.preview-description {
  display: none;
  width: 114px;
  font-family: DM Sans;
  font-size: 8px;
  line-height: 150%;
  color: #ffffff;

  @media screen and (min-width: 768px) {
    display: block;
    margin-left: 120px;
    margin-top: 66px;
  }

  @media screen and (min-width: 1200px) {
    width: 200px;
    font-size: 14px;
    line-height: 140%;
    margin-left: 180px;
    margin-top: 112px;
  }
  .btn-new-page {
    margin: 0;
    margin-top: 20px;
    width: 26px;
    height: 26px;

    @media screen and (min-width: 768px) {
      width: 18px;
      height: 18px;
      margin-top: 7px;
    }

    @media screen and (min-width: 1200px) {
      margin-top: 20px;
      width: 26px;
      height: 26px;
    }

    .products-link-icon {
      width: 8px;
    }
  }
}

.preview {
  background-color: #ffb8ca;

  .container {
    height: 502px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #ffb8ca;
    background-image: url(../images/preview/ice.png),
      url(../images/preview/preview-circle.png);
    background-repeat: no-repeat;
    background-position: bottom 0px right 32px, right 0px top 83px;

    @media screen and (min-width: 768px) {
      align-items: start;
      justify-content: start;
      height: 400px;
      padding-top: 102px;
      background-image: url(../images/preview/prev-bg-ice.png),
        url(../images/preview/prev-bg-girl.png),
        url(../images/preview/prev-bg-sircle.png),
        url(../images/preview/prev-bg-milk.png);
      background-repeat: no-repeat;
      background-position: left 378px bottom 0, right 34px bottom 0,
        left 307px top 31px, left 34px bottom 28px;
    }

    @media screen and (min-width: 1200px) {
      height: 665px;
      padding: 0 40px;
      padding-top: 171px;
      background-image: url(../images/preview/prev-bg-ice-desc.png),
        url(../images/preview/prev-bg-girl-desc.png),
        url(../images/preview/prev-bg-sircle-desc.png),
        url(../images/preview/prev-bg-milk-desc.png);
      background-position: left 480px bottom 0, right 35px bottom 0,
        left 360px top 52px, left 30px bottom 45px;
    }
  }
}

.preview-title-wrapper {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.preview-button {
  display: flex;
  .button {
    @media screen and (max-width: 1999px) {
      font-size: 8px;
      line-height: 10px;
      font-weight: 400;
      padding: 11px 14px;
      height: 24px;
    }
    @media screen and (min-width: 1200px) {
      font-size: 14px;
      line-height: 18px;
      padding: 11px 22px;
      height: 40px;
    }
  }
  button:first-child {
    margin-right: 8px;
  }
}
